<template>
<div>
  <ul class="tiemList tiemList3">
    <li  v-for="(item,index) in tiemList" :class="active==index?'active':''"  @click="tiemClick(index)">{{item}}</li>
  </ul>
  <div class="datetimeBack" v-show="dateTime">

     <div class="datetime">
       <div class="TimeName">{{TimeName}}</div>
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        :min-date="minDate"
        @confirm="confirm"
        @cancel="cancel"

      />
    </div>
  </div>

</div>
</template>

<script>
  export default {
    components:{
    },
    data() {
      return {
        TimeName:"开始时间",
        timeShow:false,//开始时间和结束时间的切换
        timeData:"",//存放开始时间

        dateTime:false,
        currentDate: new Date(),
        minDate:new Date(2017, 0, 1),
        Tiem:this.common.timeForMat(7,false),
        active: 1,
        tiemList:["昨日","近一周","近1月","近3月","自定义"]
      }
    },
    methods:{
      tiemClick(index){
        this.active=index;
        var data = new Date();
        if(index==0){
          this.Tiem=this.common.timeForMat(1,false)+" - "+this.common.timeForMat(1,false);
        }else if(index==1){
          this.Tiem=this.common.timeForMat(7,false);
        }else if(index==2){
          this.Tiem=this.common.Tiem(data,1);
        }else if(index==3){
          this.Tiem=this.common.Tiem(data,3);
        }else if(index==4){
          this.dateTime=true;
        }
        if(index!=4){
          this.$emit("timeChang",this.Tiem);
        }
      },
      confirm(data){//完成
        this.dateTime=false;
        if(this.TimeName=="结束时间"){
          this.TimeName="开始时间";
          this.minDate=new Date(2017, 0, 1);
          this.Tiem=this.timeData+" - "+this.common.Tiem(data)
          this.$emit("timeChang",this.Tiem);
        }else{

          setTimeout(()=>{
            this.dateTime=true;
            this.timeData=this.common.Tiem(data);
            this.TimeName="结束时间";
            this.minDate=data;
          },100);
        }
      },
      cancel(data){//取消
        this.dateTime=false;
        this.timeData="";
        this.TimeName="开始时间"
      }
    },
  }
</script>

<style scoped>
  .TimeName{
    position: absolute;
    top: 0;
    left: 50%;
    z-index: 9999999;
    line-height: 88px;
    width:200px;
    text-align: center;
    margin-left: -100px;

  }
  .datetimeBack{
    height: 100%;
    width: 100%;
    background:rgba(0,0,0,0.5);
    position: fixed;
    bottom: 100px;
    left: 0;
    z-index: 999999999;
  }
.datetime{
  width: 100%;
  position: fixed;
  bottom: 100px;
  left: 0;

}
</style>



